* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  max-width: 968px;
  margin: 0 auto;
  padding: 0 1rem;
  min-height: 100vh;
}

p {
  margin: 0;
}

.header {
  min-height: 30vh;
}

.main {
  display: flex;
  flex-grow: 1;
  padding: 1rem 0;
}

.aside {
  min-width: 200px;
}

.section {
  flex-grow: 1;
}

.footer {
  background-color: #111;
  text-align: center;
  color: #efefef;
  text-transform: uppercase;
  padding: 10px 0;
  letter-spacing: 3px;
}
.header {
    position: relative;
    overflow: hidden;
}

.header img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
ul.nav {
  list-style: none;
  text-transform: uppercase;
  padding: 0;
}

ul.nav a {
  display: block;
  text-decoration: none;
  color: #111;
  font-weight: bold;
  padding: 5px 10px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

ul.nav a:hover {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}
