div.productsList ul {
    list-style: square;
    padding: 0 1rem;
}

div.productsList ul a {
    text-transform: uppercase;
    text-decoration: none;
    color: #222;
}

div.productsList ul a:hover {
    text-decoration: underline;
}
div.contact-form textarea {
    display: block;
    width: 80%;
    min-height: 150px;
    resize: none;
    font-size: 1.1rem;
    line-height: 1.5;
}

div.contact-form button {
    margin-top: 1rem;
    padding: 10px 15px;
    background-color: transparent;
    border: 2px solid #111;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    -webkit-transition: .2s all;
    transition: .2s all;
}

div.contact-form button:hover {
    color: burlywood;
    background-color: #111;
}
